.catalog {
  display: flex;
  flex-wrap: wrap;
  width: $content-width;
  margin: auto;
  padding: 50px 0;

  &__card {
    margin-right: $card-margin;
    margin-bottom: 46px;
  }
  
  &__card:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }
  
  &__card:nth-child(4n) {
    margin-right: 0;
  }
}
